import React from 'react'
import ab01 from './images/spacejoy-h2_3dL9yLpU-unsplash.jpg'

export default function About() {
   return (
      <>
         <section style={{ marginBottom: "14%" }}>
            <div className='Acontainer' id='Bcontainer_1'>
               <div className='Bcontent' id='Bcontent_1'>
                  <h3>Our Story</h3>
                  <p>
                     DreamDecor is a team of smart, passionate and creative problem solvers. Were an independent agency with the energy and enthusiasm of a startup and a deep appreciation for our 45-year history, even as we keep our sights set squarely on the future. We thrive on generating wildly creative ideas and then transforming them into real and measurable results.
                     <br />
                     <br />

                     Every task, project and entry on our to-do list is an opportunity to think bigger and deliver not just great work, but better performing work. Our relationships with our clients  and each other  are open, honest and collaborative. And because of that, were not afraid to embrace bold strategies or make tough decisions. We learn from failures, we stay humble in our success and we never, ever stop asking what if?

                  </p>

               </div>
               <div className="Bcontent_img" id='Bcontent_1img'>
                  <img src={ab01} alt="" style={{ borderRadius: "10px", margin: "31px 0px",padding:"0px 8px" }} />
               </div>

            </div>
         </section>

      </>
   )
}
