import React from 'react'
import 'react-toastify/dist/ReactToastify.css';
import { useDispatch, useSelector } from 'react-redux'
import { add, OneIncrease } from '../store/cartSlice'
import { useState, useEffect } from 'react'
import { Link } from "react-router-dom";

const Products = () => {
  const dispatch = useDispatch();

  const [activelink, setActivelink] = useState("All");
  const [range, setRange] = useState(4000);
  const [type, setType] = useState("");
  const [down, setDown] = useState("Select price range");


  const pro = useSelector((state) => state.cart)

  const [products, setProduct] = useState(pro.productData);


  const filterResult = () => {

    const result = pro.productData.filter((curData) => {

      if (type === "") {
      
        return curData;

      }

      else {
        return curData.Category === type;
      }



    })

    if (range === 1) {

      result.sort((a, b) => {

        return a.price - b.price
      });

    }
    else if (range === 2) {

      result.sort((a, b) => {

        return b.price - a.price
      });

    }

    setProduct(result)

  }



  useEffect(() => {

    filterResult()
    // eslint-disable-next-line
  }, [])

  useEffect(() => {

    filterResult()

    
    // eslint-disable-next-line
  }, [range, type])



  const CartColor = (prod) => { }


  const handleAdd = (product) => {
    dispatch(add(product));
    dispatch(OneIncrease(product));

  }
// eslint-disable-next-line
  const [color, setColor] = useState({

    backgroundColor: 'green',
    color: 'white',

  });

  return (
    <>
      <main >


        <div className='Category-container'>
          <h3>Category</h3>
          <div className='Category-btn-container'>

            <button className='Category-btn' style={activelink === "All" ? { color: "#49c5b6" } : {}} onClick={() => { setType(""); setActivelink("All") }} >All</button>
            <button className='Category-btn' style={activelink === "bathroom" ? { color: "#49c5b6" } : {}} onClick={() => { setType("bathroom"); setActivelink("bathroom") }}>bathroom</button>
            <button className='Category-btn' style={activelink === "Bedroom" ? { color: "#49c5b6" } : {}} onClick={() => { setType("Bedroom"); setActivelink("Bedroom") }} >Bedroom</button>
            <button className='Category-btn' style={activelink === "Garden" ? { color: "#49c5b6" } : {}} onClick={() => { setType("Garden"); setActivelink("Garden") }} >Garden</button>
            <button className='Category-btn' style={activelink === "Living Room" ? { color: "#49c5b6" } : {}} onClick={() => { setType("Living Room"); setActivelink("Living Room") }} >Living Room</button>
            <button className='Category-btn' style={activelink === "Kitchen" ? { color: "#49c5b6" } : {}} onClick={() => { setType("Kitchen"); setActivelink("Kitchen") }} >Kitchen</button>
            <button className='Category-btn' style={activelink === "Office" ? { color: "#49c5b6" } : {}} onClick={() => { setType("Office"); setActivelink("Office") }} >Office</button>

          </div>

          <div className="container" >

            <button className='btn btn-danger' onClick={() => { setRange(4000); setType(""); setActivelink("All"); setDown("Select price range") }}>Clear Filters</button>

          </div>

        </div>


        <div className="text-center container py-5  " id='productBox'>

          <div className="container d-flex  align-items-center" id='productBox_sorting' >

            <div className="container" id='length_cont'>
              <div>
                <p className=" mb-5 mt-3  ">{products.length} Products Found</p>

              </div>
            </div>

            <div className="container" >
              <hr className="hr mb-5 mt-3" />
            </div>

            <div className="container " id='sort_cont' >
              <p className=" mb-5 mt-3   ">Sort By</p>
            </div>
            <div className="dropdown hr mb-5 mt-3 ms-2 bg-transparent d-flex jus">
              <button className="btn bg-transparent dropdown-toggle" type="button" id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="false" style={{ fontSize: "0.8rem" }}>
                {down}
              </button>
              <ul className="dropdown-menu" aria-labelledby="dropdownMenuButton1">
                <li><Link className="dropdown-item" onClick={() => { setRange(1); setDown("price (lowest)") }}>price (lowest)</Link></li>
                <li><Link className="dropdown-item" onClick={() => { setRange(2); setDown("price (highest)") }}>price (highest)</Link></li>

              </ul>
            </div>
          </div>

          <div className="row">


            {
              products.map((product) => {
                return (

                  <div className="col-lg-4 col-md-12 mb-4" id="colContainer" key={product.id}>
                    <div className="card" id='cardId'>
                      <div className="bg-image hover-zoom ripple ripple-surface ripple-surface-light"
                        data-mdb-ripple-color="light">
                        <img src={product.productImage}
                          className="w-100" alt="description" />
                        <a href="#!">
                          <div className="mask">

                          </div>
                          <div className="hover-overlay">
                            <div className="mask" style={{ backgroundcolor: "rgba(251, 251, 251, 0.15)" }}></div>
                          </div>
                        </a>
                      </div>
                      <div className="card-body">

                        <h5 className="card-title fs-6 mb-3">{product.productName}</h5>

                        <div className="card-body">
                          <Link to="/Cart" style={{ textDecoration: "none" }}>

                            <div className="d-flex align-items-center pb-2 mb-1">
                              <button className="button-87" style={product.quantity > 0 ? color : {
                                backgroundImage:
                                  "linear-gradient(45deg, rgb(11 229 218) 0%, rgb(88 145 155) 51%, rgb(204 214 223) 100%)"
                              }} onClick={() => {
                                handleAdd(product)
                                CartColor(product.quantity)


                              }}>{product.quantity === 0 ? `Add to cart` : 'Added'}</button>
                            </div>
                          </Link>
                        </div>
                        <h6 className="mb-3">${product.price}</h6>
                      </div>
                    </div>
                  </div>
                )
              })

            }


          </div>

        </div>


      </main>

    </>

  )
}

export default Products
