import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {Decrement, remove, Increment, ClearCart, getCartTotal } from '../store/cartSlice';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useEffect } from 'react';
import { Link } from "react-router-dom";


const Cart = () => {


  const dispatch = useDispatch();
  const products = useSelector((state) => state.cart)
  const totalQuantity = useSelector((state) => state.cart)


  const handleRemove = (productId) => {
    dispatch(remove(productId));
  }

  const handleDecrease = (item) => {
    dispatch(Decrement(item))

  }
  const handleIncrease = (item) => {
    dispatch(Increment(item))

  }
  const handleClear = () => {

    dispatch(ClearCart())

  }
  const notify = () => toast.error(' Item removed ', {
    position: "top-right",
    autoClose: 1000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: false,
    draggable: true,
    progress: undefined,
    theme: "colored",
  });

  useEffect(() => {
    dispatch(getCartTotal());
    // eslint-disable-next-line 
  }, [products]);

  return (
    <>
      <div className="container d-flex mt-5">
        <div className='container d-flex flex-column' style={{}}  >
          {

            products.items.map((products) => (

              <div className="card m-1 mt-2 " id='coll' key={products.id}   >

                <div className="card-body d-flex flex-row " id='card_style'>
                  <div className="container col-3" id='col_style'>
                    <div className='conatainer' id='imgcon'  >
                      <img loading='lazy' src={products.productImage} className="card-img " alt="" />
                      <div className="card-header d-flex justify-content-center">
                        {products.productName}
                      </div>



                    </div>
                  </div>
                  <div className="container ">

                    <p className="card-text" id='proDis'>{products.description}</p>

                    <div className="container d-flex a justify-content-center mt-5 " id='Incrementdiv'>
                      <ul className="pagination w-50">
                        <li className="page-item">
                          <button className="page-link " aria-label="Previous" onClick={() => handleDecrease(products)}>
                            <span aria-hidden="true"><i className="fa-solid fa-minus"></i></span>
                          </button>
                        </li>
                        <li className="page-item"><button className="page-link px-4" >{products.quantity}</button></li>
                        <li className="page-item">
                          <button className="page-link" aria-label="Next" onClick={() => handleIncrease(products)}>
                            <span aria-hidden="true"><i className="fa-solid fa-plus"></i></span>
                          </button>


                        </li>
                      </ul>
                      <div className="container" id='deletbtn'>
                        <button className="btn btn-primary" style={{ color: "red" }} id='Rembtn' onClick={() => {
                          handleRemove(products)
                          notify()
                        }}><i className="fa-solid fa-trash"></i></button>
                      </div>
                    </div>
                    <div className="container justify-content-center align-content-center">
                      <p className="card-text ms-5 mt-2 ps-3" id='price'>${products.price}</p>
                    </div>
                    {/*  */}
                  </div>

                </div>

              </div>


            ))

          }



          <div className="container" style={{ display: products.items.length === 0 ? 'none' : 'block' }} id='conbtn'>
            <Link to="/products" id='conbtnLink'>

              <button className="btn btn-primary ">Continue shoping</button>


            </Link>
          </div>





          <Link to="/products" id='fillbtnLink'>
            <div className="container" style={{ display: products.items.length === 0 ? 'flex' : 'none' }} id='fillbtn'>
              <h2>Your cart is empty</h2>

              <button className="btn btn-primary " id='fillit' style={{ border: "none" }}>FILL IT</button>


            </div>
          </Link>

          <div className="container mt-4 " id='shop' style={{ display: products.items.length === 0 ? 'none' : null }}>
            <div className="col-lg-4 col-xl-3 border  p-3 " id='shopcon'>
              <div className="d-flex justify-content-between" style={{ fontWeight: "500" }}>
                <p className="mb-2">Total items:</p>
                <p className="mb-2">{totalQuantity.totalQuantity}</p>
              </div>
              <div className="d-flex justify-content-between" style={{ fontWeight: "500" }}>
                <p className="mb-2">Subtotal</p>
                <p className="mb-2">${totalQuantity.totalamount}</p>
              </div>

              <div className="d-flex justify-content-between" style={{ fontWeight: "500" }}>
                <p className="mb-0">Shipping</p>
                <p className="mb-0">$2.99</p>
              </div>

              <hr className="my-4" />

              <div className="d-flex justify-content-between mb-4" style={{ fontWeight: "500" }}>
                <p className="mb-2">Total (tax included)</p>
                <p className="mb-2">${totalQuantity.totalamount + 2}</p>
              </div>


              <button className="btn btn-primary " style={{ backgroundColor: "red", color: "black", display: products.items.length === 0 ? 'none' : 'flex' }} id='Rembtn' onClick={(

              ) => {

                handleClear(products)
              }}>Clear Cart</button>


            </div>

          </div>

        </div>
      </div>

    </>
  )
}

export default Cart
