import React from 'react'
import { useState, useEffect, useRef } from 'react'
import{ BsFillCartFill } from 'react-icons/bs'
import{ RiContactsFill } from 'react-icons/ri'
import { Link } from "react-router-dom";
import { useSelector } from 'react-redux'

export default function Navbar() {
  const addedItmes = useSelector((state)=> state.cart)

  const [ham, Setham] = useState('fas fa-bars')

  const [style, Setstyle] = useState({

    Transition: "all 0.7",
    right: "-604px"

  });



  let menuRef = useRef();

  const manage = (e) => {

    if (menuRef && menuRef.current && menuRef.current.contains(e.target)) {

      Setham('fas fa-times')
      Setstyle({ right: "0px" })


    }
    else {
      Setham('fas fa-bars')
      Setstyle({ right: "-604px" })

    }

  }

  useEffect(() => {
    document.addEventListener('click', manage)
    return () => {
      document.removeEventListener('click', manage)
    }
  }, [])

  const handleClick = (e) => {
    e.stopPropagation();

    if (ham === 'fas fa-bars') {
      Setham('fas fa-times')
      Setstyle({ right: "0%" })

    }
    else {
      Setstyle({ right: "-604px" })
      Setham('fas fa-bars')

    }
  }


  return (
    <>


      <div>
        <nav>

          <div className="logoContainer">
            <h2 className='logo'><span>Dream</span>Decor</h2>
          </div>

          <div id='mobile' ref={menuRef}>
            <i id='bar' onClick={handleClick} className={ham} ></i>
          </div>

          <div className='linkContainer' style={style}>

            <ul>
              <li><Link className='A' to="/">Home</Link></li>
              <li><Link className='A' to="/Products">Products</Link></li>
              <li><Link className='A' to="/About">About Us</Link></li>
          <div id='Shop' >

            <Link to="/Cart" className='add' id='addCart'>Cart <i><BsFillCartFill/><span className='CartQ'>{addedItmes.items.length}</span></i></Link>
            <Link to="/Login" className='add' id='login'>Login <i><RiContactsFill/></i></Link>
            
          </div>
             
            </ul>

          </div>

       
        </nav>

      </div>
    </>
  )
}
