
import { Data } from '../components/Data'
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const { createSlice } = require('@reduxjs/toolkit');

const notifyAdded = () => toast.success(' Item added ', {
    position: "top-right",
    autoClose: 1000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: false,
    draggable: true,
    progress: undefined,
    theme: "colored",
    });
const notifyIncrement  = () => toast.success('Increase product quantity', {
    position: "top-right",
    autoClose: 1000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: false,
    draggable: true,
    progress: undefined,
    theme: "colored",
    });


const result = Data.map((el) => {
    const o = Object.assign({}, el)
    o.quantity = 0
    return o
    
})

const initialState={
    items:[],
    productData:result,
    totalQuantity:0,
    totalamount:0
     
}


const cartSlice = createSlice({
    
    name: 'cart',
    initialState,
    reducers: {
        
        add:(state, action)  =>{
                      
            let find = state.items.findIndex((item) => item.id === action.payload.id);
            let find2 = state.productData.findIndex((item) => item.id === action.payload.id);
                 
            if(find >= 0) {
              
                state.items[find].quantity += 1;
                state.productData[find2].quantity += 1;

                notifyIncrement()
                           
            }
           
            else{
                
                state.items.push(action.payload)
                
                notifyAdded ()
                                  
            }
            
        }, 
        OneIncrease:(state,action)=>{

            let find2 = state.productData.findIndex((item) => item.id === action.payload.id);
            let find = state.items.findIndex((item) => item.id === action.payload.id);
            
            
            if(state.productData[find2].quantity===0){
                state.productData[find2].quantity = 1;
                state.items[find].quantity = 1;
            }
            else{
                
            }
                
            
           

        },
        
    getCartTotal: (state) => {
        let { totalQuantity, totalPrice } = state.items.reduce(
          (cartTotal, cartItem) => {
            console.log("carttotal", cartTotal);
            console.log("cartitem", cartItem);
            const { price, quantity } = cartItem;
            console.log(price, quantity);
            const itemTotal = price * quantity;
            cartTotal.totalPrice += itemTotal;
            cartTotal.totalQuantity += quantity;
            return cartTotal;
          },
          {
            totalPrice: 0,
            totalQuantity: 0,
          }
        );
        state.totalamount= parseInt(totalPrice.toFixed(2));
        state.totalQuantity = totalQuantity;
      },
        remove:(state,action)=>{

            
            let find2 = state.productData.findIndex((item) => item.id === action.payload.id);
            state.items = state.items.filter((item) => item.id !== action.payload.id);
  
            state.productData[find2].quantity = 0;
          

        },
        Increment:(state,action)=>{
            let find2 = state.productData.findIndex((item) => item.id === action.payload.id);
            
            const itemIndex = state.items.findIndex((item) => item.id === action.payload.id);
            console.log(action.payload)
            if(state.items[itemIndex].quantity>=1){
                state.items[itemIndex].quantity +=1;
                state.productData[find2].quantity += 1;
                state.totalQuantity +=1;
                state.totalamount+=action.payload.price

            }


        },
        Decrement:(state,action)=>{
            let find2 = state.productData.findIndex((item) => item.id === action.payload.id);

            const itemIndex = state.items.findIndex((item) => item.id === action.payload.id);
            if(state.items[itemIndex].quantity>1){
                state.items[itemIndex].quantity -=1
                state.productData[find2].quantity -= 1;
                state.totalQuantity -=1;
                state.totalamount-=action.payload.price

            }

          else if(state.items[itemIndex].quantity === 1){
            state.items = state.items.filter((item) => item.id !== action.payload.id);
            state.productData[find2].quantity -= 1;
            state.totalQuantity -=1;
            state.totalamount-=action.payload.price
          } 

        },

        setZero:(state)=>{
             
                return state.productData.quantity=0
                        
            },
            
        ClearCart:(state)=>{

            state.totalQuantity = 0;
            state.totalamount=0
            for (let i = 0; i < state.productData.length; i++) {

                 state.productData[i].quantity = 0

             }

             state.items = []
             
            }
            
    },
});

export const { OneIncrease,setZero,add, remove,Increment,Decrement,ClearCart,getCartTotal} = cartSlice.actions;
export default cartSlice.reducer;